<template>
    <div>
        <div class="row">
        <div class="container">
          <b-card
            overlay
            img-src="https://picsum.photos/900/250/?image=3"
            img-alt="Card Image"
            text-variant="white"
            :title="message"
            sub-title="Equipo Archuletas"
          >
            <b-card-text>
              Ingresa a la app para autenticarte
            </b-card-text>
            <b-spinner v-if="!stop" style="width: 3rem; height: 3rem;" label="Large Spinner"></b-spinner>
          </b-card>
        </div>
        </div>
        <hr>
        <div class="row">
        <b-card class="container" v-if="ok" :title="information.name" header-tag="header" footer-tag="footer">
          <template v-slot:header>
            <div class="mb-2">
              <b-avatar :src="face" size="6rem"></b-avatar>
            </div>
          </template>
          <b-card-text>{{information.email}}</b-card-text>
          <b-card-text>{{information.direction}}</b-card-text>
          <b-card-text>{{information.nationality}}</b-card-text>
          <b-card-text>Numero de tarjeta: {{information.card_number}}</b-card-text>
          <b-card-text>Numero de cuenta: {{information.account_number}}</b-card-text>
        </b-card>
        </div>
    </div>
</template>
<script>
    import ApiService from '@/services/api.service.js';

    import { urls } from '@/api.urls.js';
    import { TokenService } from '@/services/storage.service.js'
    export default {
        data () {
          return {
              w:'',
            error:false,
            stop:false,
            form:{
              "password":"",
              "username":"",
            },
            dismissSecs: 30,
            dismissCountDown: 0,
            message:'Esperando autenticación',
            status:'',
            ok:false,
            face:null,
            information:{
                name:null,
                email:null,
                direction:null,
                nationality:null,
                card_number:null,
                account_number:null,
            }
            
          }
        },
        created(){
              var tkn = TokenService.getToken();
              var con = ""
              for(let i of tkn){
                  if (i == "@"){
                      break
                  }
                  con += i
              }
              var url =  `wss://hydra-ws.abstract-lab.com/ws/${con}/` 
              
              var exampleSocket = new WebSocket(url);
              
              exampleSocket.onopen =  function(e) {
                    
                    
              };
              exampleSocket.onmessage = (event) => {
                  var message = JSON.parse(event.data).message
                  
                  if(message.command=="ok"){
                      this.message = "Autenticación exitosa"
                      this.info();
                  }else{
                      this.message = "Autenticación fallida"
                      
                      
                  }
                  this.stop = true;
                  
              }
              exampleSocket.onclose = function(event) {
                  if (event.wasClean) {
                    alert(`[close] Connection closed cleanly, code=${event.code} reason=${event.reason}`);
                  } else {
                    // e.g. server process killed or network down
                    // event.code is usually 1006 in this case
                    alert('[close] Connection died');
                  }
              };
                
              exampleSocket.onerror = function(error) {
                  alert("Error al conectar con websockets");
                  
              };
              
              
              
        },   
        methods:{
            info(){
                const server = "http://ec2-18-223-113-64.us-east-2.compute.amazonaws.com:8000";
                let data = {
                    "email":TokenService.getToken()
                }
                ApiService.post(urls.information, data).then(response=>{
                  //console.log(response.data);
                  //console.log(server + "/media/" + response.data[0].face_1);
                  this.face =  server + "/media/" + response.data[0].face_1;
                  this.information.name = response.data[0].username;
                  this.information.email = response.data[0].email;
                  this.information.direction=response.data[0].direction,
                  this.information.nationality=response.data[0].nationality,
                  this.information.card_number=response.data[0].card_number,
                  this.information.account_number=response.data[0].account_number,
                  
                  this.ok = true;
                  
                });
            },
            
            
            
        }
        
    }
</script>


